















import Home from './components/Home.vue'
import Roster from './components/Roster.vue'

App =
  name: 'app'
  created: () ->
    @$router.addRoutes [
      { path: '/', component: Home, name: 'home' }
      { path: '/build/:factionId', component: Roster, props: true }
    ]

export default App




